/* global document, Office, console */

// 1. Í∞ÄÏû• Î®ºÏ†Ä Ïã§ÌñâÎêòÎäî Ï§ÄÎπÑ Ìï®Ïàò
Office.onReady((info) => {
  // Î°úÍ∑∏: Office Ï§ÄÎπÑ ÏôÑÎ£å
  console.log("Office.onReady. Host: " + info.host);

  // Ìò∏Ïä§Ìä∏Í∞Ä ÏõêÎÖ∏Ìä∏Ïùº ÎïåÎßå Ïã§Ìñâ
  if (info.host === Office.HostType.OneNote) {

    // Î≤ÑÌäº Ïó∞Í≤∞ÌïòÍ∏∞
    const btn = document.getElementById("insert-template");

    if (btn) {
      // Î≤ÑÌäºÏù¥ ÏûàÏúºÎ©¥ ÌÅ¥Î¶≠ Ïù¥Î≤§Ìä∏ Ïó∞Í≤∞
      btn.onclick = run;
    } else {
      console.error("No button error");
    }
  }
});

// 2. Ïã§Ï†ú Í∏∞Îä•ÏùÑ ÏàòÌñâÌïòÎäî Ìï®Ïàò
async function run() {
  // 1. Get input values
  const textArea = document.getElementById("custom-input");
  const statusText = document.getElementById("status"); // Element to display status messages
  const rawText = textArea.value;

  // 2. If input is empty, show message and stop
  if (!rawText) {
    statusText.innerText = "‚ö†Ô∏è Input is empty!";
    statusText.style.color = "red";
    return;
  }

  try {
    await OneNote.run(async (context) => {
      // Notify start of operation
      statusText.innerText = "‚è≥ Creating pages...";
      statusText.style.color = "blue";

      const lines = rawText.split("\n").filter(line => line.trim() !== "");
      const currentSection = context.application.getActiveSection();

      let count = 0;
      lines.forEach((title) => {
        currentSection.addPage(title.trim());
        count++;
      });

      await context.sync();

      // ‚òÖ Show result as text instead of alert
      statusText.innerText = "‚úÖ Success: " + count + " pages created!";
      statusText.style.color = "green";

      // Clear input field
      textArea.value = "";

      // (Optional) Clear status message after 3 seconds
      setTimeout(() => {
        statusText.innerText = "";
      }, 3000);
    });
  } catch (error) {
    console.error("Failed: " + error);
    // Display error message as text
    statusText.innerText = "‚ùå Error: " + error.message;
    statusText.style.color = "red";
  }
}